<template>
  <div>
    <h2>Date: {{ date }}</h2>
    <h2>Week: {{ week }}</h2>
    <button @click="clear">Clear all</button>
    <table border="1" cellspacing="0" cellpadding="5">
      <thead>
        <tr>
          <th></th>
          <th v-for="(a, i) in accounts" :key="i">
            {{ a }}
          </th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(e, eid) in events" :key="eid">
          <td>{{ e }}</td>
          <th v-for="(a, aid) in accounts" :key="aid">
            <input
              type="checkbox"
              :checked="isDone(eid, a)"
              @change="toggle(eid, a, $event)"
            />
          </th>
        </tr>
      </tbody>
    </table>

    <h3>Weekly events</h3>
    <table border="1" cellspacing="0" cellpadding="5">
      <thead>
        <tr>
          <th></th>
          <th v-for="(a, i) in accounts" :key="i">
            {{ a }}
          </th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(e, eid) in weekEvents" :key="eid">
          <td>{{ e }}</td>
          <th v-for="(a, aid) in accounts" :key="aid">
            <input
              type="checkbox"
              :checked="isDoneWeek(eid, a)"
              @change="toggleWeek(eid, a, $event)"
            />
          </th>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import dataMixin from "../dataMixin";
export default {
  mixins: [dataMixin],
  created() {
    this.load();
    console.log(this.date);
  },
  methods: {
    toggle(eid, acc, e) {
      const tracker = this.trackers.find((t) => t.acc === acc);
      console.log(eid, tracker);
      if (!tracker) {
        this.trackers.push({ acc, events: [eid] });
        return;
      }

      if (e.target.checked) {
        !tracker.events.includes(eid) && tracker.events.push(eid);
      } else {
        tracker.events = tracker.events.filter((i) => i !== eid);
      }
    },
    isDone(eid, acc) {
      const tracker = this.trackers.find((t) => t.acc === acc);
      return tracker && tracker.events.includes(eid);
    },
    toggleWeek(eid, acc, e) {
      const tracker = this.weekTrackers.find((t) => t.acc === acc);
      console.log(eid, tracker);
      if (!tracker) {
        this.weekTrackers.push({ acc, events: [eid] });
        return;
      }

      if (e.target.checked) {
        !tracker.events.includes(eid) && tracker.events.push(eid);
      } else {
        tracker.events = tracker.events.filter((i) => i !== eid);
      }
    },
    isDoneWeek(eid, acc) {
      const tracker = this.weekTrackers.find((t) => t.acc === acc);
      return tracker && tracker.events.includes(eid);
    },
  },
  watch: {
    trackers: {
      deep: true,
      handler: function (newVal) {
        localStorage.setItem("trackers-" + this.date, JSON.stringify(newVal));
      },
    },
    weekTrackers: {
      deep: true,
      handler: function (newVal) {
        localStorage.setItem("w-trackers-" + this.week, JSON.stringify(newVal));
      },
    },
  },
};
</script>